@font-face {
  font-family: inter;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: inter;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: inter;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: inter;
  src: url("../fonts/Inter-Bold.woff2") format("woff2"), url("../fonts/Inter-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
:root {
  --color-primary: #002760;
  --color-title: black;
  --color-text: rgba(0, 0, 0, 0.75);
  --color-secondary: rgb(19, 50, 62);
  --color-contrast: rgba(175, 150, 135, 1);
  --color-brown: #cb9e88;
  --color-dark: #1b1919;
  --color-light: #fff2e9;
  --color-salmon: #e9856f;
  --color-cornflowerblue: #6f85e9;
  --bg-color-primary: rgba(240, 240, 240, 0.3);
  --bg-color-secondary: #fff2e9;
  --main-font-regular: normal normal 400 1rem "inter", sans-serif;
  --main-font-medium: normal normal 500 1rem "inter", sans-serif;
  --main-font-semi-bold: normal normal 600 1rem "inter", sans-serif;
  --main-font-bold: normal normal 700 1rem "inter", sans-serif;
  --main-bg-gradient: linear-gradient(90deg, rgba(135, 150, 175, 1) 0%, rgba(255, 255, 255, 0) 100%);
  --header-height: 4rem;
  --text-shadow-primary: 1px 1px 2px rgba(0, 0, 0, 0.5) ;
}
html,
body {
  box-sizing: border-box;
  margin: 0;
  font: var(--main-font-regular);
  /* background: var(--main-bg-gradient) no-repeat; */
  height: 100%;
  color: var(--color-primary);
  background-color: #FFFFFF;
}

.title-main {
  color: var(--color-title);
  font: var(--main-font-bold);
  font-size: 4rem;
  letter-spacing: -1.2px;
  margin: 0;
}

.title-secondary {
  color: var(--color-title);
  font: var(--main-font-semi-bold);
  font-size: 3rem;
  letter-spacing: -1.2px;
  margin: 0 0 2.5rem;
}

.text-main {
  color: var(--color-text);
  font: var(--main-font-regular);
  font-size: 1.5rem;
  margin: 0;
}

.divider {
  box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.5);
}
.content-wrapper {
  margin: auto;
  width: 1440px;
  box-sizing: border-box;
  position: relative;
  padding: 0 5rem;
}
.run-string {
  padding: 5rem;
}
.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: var(--header-height);
  position: sticky;
  top: 0;
  z-index: 9;
  margin-bottom: 6.25rem;
  font-size: 1.25rem;
  background-color: white;
}
.header__inner {
  display: flex;
  justify-content: space-between;
}
.header__menu {
  align-self: flex-end;
  display: flex;
  justify-content: space-between;
  gap: 1.8rem;
  padding-bottom: 0.2rem;
}

.menu__link {
  color: var(--color-primary);
  text-decoration: none;
  transition: color 0.2s;
  font: var(--main-font-medium);
}

.menu__link_active {
  color: var(--color-contrast);
}

.menu__link:hover {
  color: var(--color-contrast);
}

.logo {
  background: no-repeat center/contain url(../img/logo.svg);
  height: 2.6875rem;
  width: 8.75rem;
}
.burger-menu {
  /* position: relative;
  display: inline-block; */
  /* display: none; */
  visibility: hidden;
}

.burger-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 2.5rem;
  height: 2rem;
}

.burger-menu__line {
  background: var(--color-primary);
  height: 0.1rem;
  /* margin: .1rem; */
  width: 100%;
  transition: all 0.3s ease;
}

.burger-btn.active .burger-menu__line:nth-child(1) {
  transform: translateY(5px) rotate(45deg);
}

.burger-btn.active .burger-menu__line:nth-child(2) {
  opacity: 0;
}

.burger-btn.active .burger-menu__line:nth-child(3) {
  transform: translateY(-5px) rotate(-45deg);
}

.burger-menu__layer {
  color: var(--color-primary);
  position: absolute;
  top: var(--header-height);
  left: 0;
  right: 0;
  /* bottom: 0; */
  background-color: white;
  /* border: 1px solid #ddd; */
  /* width: 200px; */
  overflow: hidden;
  transition: all 0.3s ease;
  height: 0;
  z-index: 2;
  touch-action: none;
  box-sizing: border-box;
}

.burger-menu__layer.active {
  height: 100vh;
}

.menu-item {
  padding: 12px 15px;
  color: var(--color-primary);
  text-decoration: none;
  display: block;
  transition: background-color 0.2s;
}

.menu-item:hover {
  background-color: #f5f5f5;
}

.menu-item:not(:last-child) {
  border-bottom: 1px solid #eee;
}

@media (max-width: 1023px) {
  .burger-menu {
    visibility: visible;
  }
}
.detail-button {
  cursor: inherit;
  background-color: black;
  color: white;
  border-radius: 0.5rem;
  padding: 0.875rem 1.5rem;
  font: var(--main-font-medium);
  font-size: 1rem;
}
.home-page__header {
  margin-bottom: 7.5rem;
}
.home-page__title {
  margin-bottom: 1.5rem;
}
.home-page__description {
  line-height: 1.5;
  margin-bottom: 3rem;
}
.home-page__description-accented {
  color: var(--color-primary);
}
.home-page .home-page__button-wrapper {
  display: flex;
  justify-content: flex-end;
}
.home-page__image {
  width: 100%;
}
.last-years {
  padding: 10rem 10rem;
  box-sizing: border-box;
  background-color: var(--bg-color-primary);
}

.last-years__description {
  box-sizing: border-box;
  text-align: center;
  font: var(--secondary-font-bold);
  font-size: 3.2rem;
  letter-spacing: 0.2rem;
  /* color: var(--color-contrast); */
  color: var(--color-primary);
}

@media (min-width: 1024px) and (max-width: 1439px) {
  .last-years {
    padding: 5rem;
  }
  .last-years__description {
    font-size: 2.2rem;
  }
}
@media (max-width: 1023px) {
  .last-years {
    padding: 5vw;
  }
  .last-years__description {
    font-size: 2.2vw;
  }
}
@media (max-width: 414px) {
  .last-years {
    padding: 8vw;
  }
  .last-years__description {
    text-align: center;
    font-size: 4vw;
  }
}

.about-us__title {
  margin-bottom: 1.5rem;
}
.about-us__description {
  margin-bottom: 2.5rem;
}
.about-us__photo {
  width: 100%;
  margin-bottom: 3rem;
}
.about-us__sub-title {
  font: var(--main-font-medium);
  font-size: 1.5rem;
  line-height: 1.5;
  color: var(--color-title);
  margin-bottom: 0.5rem;
}
.about-us__text {
  color: var(--color-text);
  font: var(--main-font-regular);
  font-size: 1.5rem;
  line-height: 1.5;
  margin-bottom: 2.5rem;
}
.about-us__map {
  margin-bottom: 5rem;
  width: 100%;
}
.contacts-screen__title {
  margin-bottom: 1.5rem;
}
.contacts-screen__description {
  margin-bottom: 2.25rem;
}
.contacts-screen__logo-wrapper {
  position: fixed;
  left: 0;
  bottom: 0;
  padding: 5rem 0;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  width: 100%;
}
.contacts-screen__logo {
  width: 20rem;
}
.projects__main-title {
  margin-bottom: 4rem;
}
.projects__wrapper {
  display: grid;
  grid-template-columns: 60% 40%;
  grid-template-rows: 1fr 1fr;
  gap: 2.5rem 1rem;
  margin-bottom: 5rem;
}
.projects__wrapper-aternative {
  grid-template-columns: 40% 60%;
}
.projects__wrapper-correct {
  grid-template-rows: 50% 40%;
}
.projects__photo {
  border-radius: 0.5rem;
}
.projects__title {
  font: var(--main-font-medium);
  font-size: 1.5rem;
  color: var(--color-title);
  margin-bottom: 0.25rem;
}
.projects__address {
  color: var(--color-text);
  font: var(--main-font-regular);
  font-size: 1.25rem;
}
.projects__project {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1.5rem;
}
.projects__project-first {
  grid-row-start: 1;
  grid-row-end: 3;
}
.projects__project-second {
  grid-row-start: 1;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
}
.projects__project-third {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
}
.build-for-people {
  height: 10rem;
  background-image: url(../img/run-strings/build-for-people.png);
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 200%;
  animation: 15s run-string-building-for-people forwards infinite linear;
}

@keyframes run-string-building-for-people {
  to {
    background-position-x: -100%;
  }
}
.slider__wrapper {
  overflow: hidden;
  margin-bottom: 6.5rem;
}
.slider__lent {
  display: flex;
  gap: 1.5rem;
}
.slider__label {
  font: var(--main-font-medium);
  font-size: 1.5rem;
  color: black;
  text-align: center;
}
.command__photo {
  margin-bottom: 2.75rem;
}
.command__sub-title {
  font: var(--main-font-medium);
  font-size: 1.5rem;
  color: var(--color-title);
  margin: 0 0 0.5rem;
}
.command__description {
  font: var(--main-font-regular);
  font-size: 1.5rem;
  color: var(--color-text);
  line-height: 1.5;
}
.investo-block-run {
  height: 10rem;
  background-image: url(../img/run-strings/investo.png);
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 150%;
  animation: 15s run-string-investo forwards infinite linear;
}

@keyframes run-string-investo {
  to {
    background-position-x: -50%;
  }
}
.footer {
  border-top: 1px solid #454545;
  padding: 4.75rem 0;
  display: flex;
  justify-content: space-between;
}
.footer__lent {
  transition: transform 0.2s linear;
}
.footer__logo {
  width: 20rem;
}
.structure__title {
  margin-bottom: 3rem;
}
.structure__card-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10rem;
}
.structure__card-title {
  color: var(--color-title);
  font: var(--main-font-medium);
  font-size: 1.5rem;
  margin: 0 0 1.875rem;
}
.structure__card-body {
  color: var(--color-text);
  font: var(--main-font-medium);
  font-size: 1.5rem;
  line-height: 1.5;
}
.contacts__wrapper {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.contacts__title {
  font: var(--main-font-medium);
  font-size: 1rem;
  color: var(--color-title);
  margin: 0;
}
.contacts__text {
  font: var(--main-font-medium);
  color: var(--color-text);
  font-size: 1rem;
  margin: 0;
}